import { Component, OnInit, ChangeDetectorRef, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';
import * as _ from 'lodash';  
import * as moment from 'moment';
import { StaffService } from 'src/app/api/controllers/Staff';
import { MeetingService } from 'src/app/api/controllers/Meeting';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-meetings-create',
  templateUrl: './meetings-create.component.html',
  styleUrls: ['./meetings-create.component.scss']
})
export class MeetingsCreateComponent implements OnInit {
  @Input ('popup') data:any = {type:'create'};
  @Output () popupClose = new EventEmitter();
  meetingCategoryData:any;
  meeting:any;
  subMeeting:Array<any>  = [];
  subMeetingField = {
    meetingDate:'',
    meetingStartTime:'',
    meetingEndTime:''
  }
  showTableContent:boolean = false;
  allUsers:any;
  users:any;
  staffCategoryMap=new Map();
  roleTypedropdownSettings={}
  roleTypeArr={
    "dropdownList":[],
    "selectedItems":[]
  };
  blockdropdownSettings={}
  apartmentBlock={
    "dropdownList":[],
    "selectedItems":[]
  };
  condition={
    "dropdownList":[],
    "selectedItems":[]
  };
  roleDropdownSettings={};
  roleCategory={
    "dropdownList":[],
    "selectedItems":[]
  };
  dropdownSettings = {};
  staffSubCategory={
    "dropdownList":[],
    "selectedItems":[]
  };
  staffSubCategoryMap=new Map();
  staffCategory={
    "dropdownList":[],
    "selectedItems":[]
  };

  constructor(private lookupService: LookupService,
    private cookieService:CookieService,
    private apartmentService:ApartmentService,
    private staffService:StaffService,
    private userService: UserService,
    private meetingService: MeetingService,
    private sharedService: SharedService,
  ) {}

  // Goback(){
  //   this.router.navigate(['ams/meetings/list']);
  // }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getAppartmentRole(item){
    this.roleCategory.selectedItems=[];
    this.staffCategory.selectedItems=[];
    var params={
      'ApartmentId': parseInt(this.cookieService.get('apartmentId')),
      'RoleTypeId': item.value
    }
    this.userService.getApartmentRolesByRoleTypeId(params).subscribe((res:any) => {
      this.roleCategory.dropdownList=res;
      let conditinArr= [{"name":"IsLiving","value":1},
                        {"name":"IsPrimaryContact","value":2}];
      if(parseInt(item.value)==2 || parseInt(item.value)==4){
        this.condition.dropdownList=conditinArr;
      } else{
        this.condition.dropdownList=[];
        this.condition.selectedItems=[];
      }
      this.updateUsers();
    });
  }

  getUserIds$(){
    let roleIds= Array.prototype.map.call(this.roleCategory.selectedItems,function(item){ return item.value}).join(",");
    let blockIds= Array.prototype.map.call(this.apartmentBlock.selectedItems,function(item){ return item.apartmentBlockId}).join(",");
    let conditionIds= Array.prototype.map.call(this.condition.selectedItems,function(item){ return item.value}).join(",");
    let staffcategoryIds= Array.prototype.map.call(this.staffCategory.selectedItems,function(item){ return item.value}).join(",");
    let staffSubCategoryIds= Array.prototype.map.call(this.staffSubCategory.selectedItems,function(item){ return item.value}).join(",");
    let params={
      'roleIds':roleIds,
      'conditionIds':conditionIds,
      'appartmentbBlockIds':blockIds,
      'apartmentId':parseInt(this.cookieService.get('apartmentId')),
      'staffcategoryIds':staffcategoryIds,
      'staffSubCategoryIds':staffSubCategoryIds
    }  
    return this.userService.getUsersByFilter(params);
  }

  updateUsers(){
    this.getUserIds$().subscribe((res:any)=>{
        if(res.length>0){
        this.users=this.allUsers.filter(function(obj){
        return res.includes(obj.userId);
      });
      }else{
        this.users=[];
      }
    });
  }

  getstaffCategories(item){
    if(this.roleTypeArr.selectedItems[0].name.toLowerCase()=="staff") {
      if(this.staffCategoryMap.get(item.value)==undefined){
        let params = {
          'roleId':item.value,
          'apartmentId': parseInt(this.cookieService.get('apartmentId'))
        }
        this.staffService.getStaffCategoryByRole(params).subscribe((res:any) => {
          let temp=[...this.staffCategory.dropdownList];
            res.forEach(element => {
              element.category=item.name;
              temp.push(element);
            });
            this.staffCategoryMap.set(item.value,res);
            this.staffCategory.dropdownList=temp;
            this.updateUsers();
         });
      } else {
        let temp=[...this.staffCategory.dropdownList];
        let addStaffCategory= this.staffCategoryMap.get(item.value);
        addStaffCategory.forEach(element => {
          temp.push(element);
        });
        this.staffCategory.dropdownList=temp;
      }
    }
    this.updateUsers();
  }

  getAllStaffCategories(items){
    items.forEach(element => {
     this.getstaffCategories(element);
    });
  }

  getStaffSubCategories(item){
    if(this.staffSubCategoryMap.get(item.value)==undefined){
      let params={
       'categoryId':item.value,
       'apartmentId': parseInt(this.cookieService.get('apartmentId'))
      }
      this.staffService.getSubStaffCategoryByCategory(params).subscribe((res:any) => {
        let temp=[...this.staffSubCategory.dropdownList];
        res.forEach(element => {
          element.category=item.name;
          temp.push(element);
        });
        this.staffSubCategoryMap.set(item.value,res);
        this.staffSubCategory.dropdownList=temp;
        this.updateUsers();
      });
     } 
     else{
      let addSubStaffCategory= this.staffSubCategoryMap.get(item.value);
      let temp=[...this.staffSubCategory.dropdownList];
      addSubStaffCategory.forEach(element => {
      temp.push(element);
      });
      this.staffSubCategory.dropdownList=temp;
      this.updateUsers();
    }
 }

  removeStaffCategories(item){
    if(this.roleTypeArr.selectedItems[0].name.toLowerCase()=="staff") {
      let removeStaffCategory= this.staffCategoryMap.get(item.value);
      removeStaffCategory.forEach(element => {
          this.staffCategory.dropdownList=this.staffCategory.dropdownList.filter(function(obj){
            return obj.value!==element.value;
          });
      });
    }
    this.updateUsers();
  }

  removeAllStaffCategories(items){
    this.roleCategory.selectedItems=[];
    this.staffCategory.dropdownList=[];
    this.staffCategory.selectedItems=[];
    this.updateUsers();
  }

  removeRole(item){
    this.roleCategory.dropdownList=[];
    this.condition.dropdownList=[];
    this.condition.selectedItems=[];
    this.removeAllStaffCategories([]);
    this.removeAllSubStaffCategories([]);
  }

  removeAllSubStaffCategories(items){
    this.staffSubCategory.dropdownList=[];
    this.staffCategory.selectedItems=[];
    this.updateUsers();
  }

  removeSubStaffCategories(item){
    let removeSubStaffCategory= this.staffSubCategoryMap.get(item.value);
    removeSubStaffCategory.forEach(element => {
      this.staffSubCategory.dropdownList=this.staffSubCategory.dropdownList.filter(function(obj){
        return obj.value!==element.value;
      });
    });
    this.updateUsers();
  }

  removeGroupSubStaffCategories(items){
    items.list.forEach(element => {
      this.removeSubStaffCategories(element);
    });
    this.updateUsers();
  }

  getAllSubStaffCategories(items){
    items.forEach(element => {
     this.getStaffSubCategories(element);
    });   
  }

  getGroupSubStaffCategories(items){
    items.list.forEach(element => {
      this.getStaffSubCategories(element);
    });
    this.updateUsers();
  }

  showTable() {
    this.showTableContent = true;
  }

  addMeetingSchedule() {
    if(this.data.type == 'edit' && this.subMeeting.length < 1) {
      this.addSchedule();
    }
    if(this.data.type == 'create') {
      this.addSchedule();
    }
  }

  addSchedule() {
    if(this.subMeetingField.meetingDate && this.subMeetingField.meetingStartTime && this.subMeetingField.meetingEndTime) {
      let entity = {
        meetingDate : moment(this.subMeetingField.meetingDate).format(),
        meetingStartTime : moment(this.subMeetingField.meetingStartTime).format('HH:mm'),
        meetingEndTime: moment(this.subMeetingField.meetingEndTime).format('HH:mm'),
      };
      this.subMeeting.push(entity);
      this.subMeetingField.meetingDate = '';
      this.subMeetingField.meetingStartTime = '';
      this.subMeetingField.meetingEndTime = '';
    }
  }

  editMeetingScheduleRow(data,index) {
    this.subMeetingField.meetingDate = moment(data.meetingDate).format()
    this.subMeetingField.meetingStartTime =  moment(data.meetingStartTime,'HH:mm').format();
    this.subMeetingField.meetingEndTime = moment(data.meetingEndTime,'HH:mm').format();;
    this.subMeeting.splice(index, 1);
  }

  deleteMeetingScheduleRow(index){
    this.subMeeting.splice(index, 1);
  }

  onCreate() {
    let apartment = this.apartmentBlock.selectedItems.reduce((acc,data,index)=>{
      if(index == 0) {
        return JSON.stringify(data.apartmentBlockId);
      } else {
        return acc+','+JSON.stringify(data.apartmentBlockId);
      }
    },'');
    let condition =  this.condition.selectedItems.reduce((acc,data,index)=>{
      if(index == 0) {
        return JSON.stringify(data.value);
      } else {
        return acc+','+JSON.stringify(data.value);
      }
    },'');
    let roleIds =  this.roleCategory.selectedItems.reduce((acc,data,index)=>{
      if(index == 0) {
        return JSON.stringify(data.value);
      } else {
        return acc+','+JSON.stringify(data.value);
      }
    },'');
    let params:any = {};
    params.meetingFilters_model = {
      "filterId": 0,
      "roleTypeId": Number(this.roleTypeArr.selectedItems.length > 0 ? this.roleTypeArr.selectedItems[0].value : 0),
      "apartmentBlockIds": String(apartment ? apartment : '0'),
      "conditions": String(condition ? condition: '0'),
      "roleIds": String(roleIds),
      "staffCategoryIds": '0',
      "subStaffCategoryIds": '0',
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null
    }
    this.meetingService.upsertMeetingFilters(params).subscribe((res:any)=>{
      if(typeof(res.message) == 'number' ) {
        let users = this.users.reduce((acc,data,i)=>{
          if(i == 0) {
            return JSON.stringify(data.userId);
          } else {
            return acc+','+JSON.stringify(data.userId);
          }
        },'');
        let params:any = {};
        for(let i= 0; i<this.subMeeting.length; i++) {
          params.meetings = {
            meetingId: 0,
            filterId: res.message,
            apartmentId: parseInt(this.cookieService.get('apartmentId')),
            meetingTypeId: parseInt(this.meeting.meetingTypeId),
            meetingCategoryId: null,
            meetingRecipientsIds: users,
            meetingDate: this.subMeeting[i].meetingDate,
            fromTime: this.subMeeting[i].meetingStartTime,
            toTime: this.subMeeting[i].meetingEndTime,
            subject : this.meeting.meetingSubject,
            message : this.meeting.meetingMessage,
            comments: "string",
            isActive: true,
            insertedBy: parseInt(this.cookieService.get('userId')),
            insertedOn: new Date().toISOString(),
            updatedBy: null,
            updatedOn: null,
            meetingStatusId: null,
            meetingAttendees: "string",
            meetingminutes: "string",
            actionitems: "string"
          }
          this.meetingService.addMeeting(params).subscribe((res)=>{
            this.sharedService.setAlertMessage("Meeting Created successfully");
            this.popupClose.emit(true);
          })
        }
      }
    })
  }

  onUpdate() {
    let apartment = this.apartmentBlock.selectedItems.reduce((acc,data,index)=>{
      if(index == 0) {
        return JSON.stringify(data.apartmentBlockId);
      } else {
        return acc+','+JSON.stringify(data.apartmentBlockId);
      }
    },'');
    let condition =  this.condition.selectedItems.reduce((acc,data,index)=>{
      if(index == 0) {
        return JSON.stringify(data.value);
      } else {
        return acc+','+JSON.stringify(data.value);
      }
    },'');
    let roleIds =  this.roleCategory.selectedItems.reduce((acc,data,index)=>{
      if(index == 0) {
        return JSON.stringify(data.value);
      } else {
        return acc+','+JSON.stringify(data.value);
      }
    },'');
    let params:any = {};
    params.meetingFilters_model = {
      "filterId": this.meeting.filterId,
      "roleTypeId": Number(this.roleTypeArr.selectedItems.length > 0 ? this.roleTypeArr.selectedItems[0].value : 0),
      "apartmentBlockIds": String(apartment ? apartment : '0'),
      "conditions": String(condition ? condition: '0'),
      "roleIds": String(roleIds),
      "staffCategoryIds": '0',
      "subStaffCategoryIds": '0',
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null
    }
    this.meetingService.upsertMeetingFilters(params).toPromise();
    let users = this.users.reduce((acc,data,i)=>{
      if(i == 0) {
        return JSON.stringify(data.userId);
      } else {
        return acc+','+JSON.stringify(data.userId);
      }
    },'');
    let meetingUpdate:any = {};
    for(let i= 0; i<this.subMeeting.length; i++) {
      meetingUpdate.meetings = {
        meetingId: this.meeting.meetingId,
        filterId: this.meeting.filterId,
        apartmentId: parseInt(this.cookieService.get('apartmentId')),
        meetingTypeId: parseInt(this.meeting.meetingTypeId),
        meetingCategoryId: null,
        meetingRecipientsIds: users,
        meetingDate: this.subMeeting[i].meetingDate,
        fromTime: this.subMeeting[i].meetingStartTime,
        toTime: this.subMeeting[i].meetingEndTime,
        subject : this.meeting.meetingSubject,
        message : this.meeting.meetingMessage,
        comments: "string",
        isActive: true,
        insertedBy: parseInt(this.cookieService.get('userId')),
        insertedOn: new Date().toISOString(),
        updatedBy: null,
        updatedOn: null,
        meetingStatusId: null,
        meetingAttendees: "string",
        meetingminutes: "string",
        actionitems: "string"
      }
      this.meetingService.updateMeeting(meetingUpdate).subscribe((res)=>{
        this.sharedService.setAlertMessage("Meeting Updated successfully");
        this.popupClose.emit(true);
      })
    }
  }

  onDismiss() {
    this.popupClose.emit(false);
  }

  ngOnInit(): void {
    this.meeting = {};
    let params = {
      LookupTypeId: 21
    }
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
  		this.meetingCategoryData = res.filter(item => {
        return item.isActive;
      });
    });
    this.userService.getAllRoleTypes().subscribe((res:any) => {
      this.roleTypeArr.dropdownList=res;
    });
    let users = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.userService.getUsersByApartmentId(users).subscribe((res)=>{
      this.allUsers=res;
      this.updateUsers();
      });
    let apartmentBlock = {
      apartmentId:  parseInt(this.cookieService.get('apartmentId'))
    }
    this.apartmentService.getApartmentBlockByApartmentId(apartmentBlock).subscribe((res:any) => {
      this.apartmentBlock.dropdownList = res;
    });
    //Meeting Edit
    if(this.data.type == 'edit') {
      let params = {
        meetingId : parseInt(this.data.id)
      }
      this.meetingService.getMeetingById(params).subscribe((res:any)=>{
        this.meeting.meetingSubject = res[0].subject;
        this.meeting.meetingMessage = res[0].message;
        this.meeting.meetingTypeId = res[0].meetingTypeId;
        this.meeting.filterId = res[0].filterId;
        this.meeting.meetingId = parseInt(this.data.id);
        let entity = {
          meetingDate : moment(res[0].meetingDate).format(),
          meetingStartTime : res[0].fromTime,
          meetingEndTime: res[0].toTime
        }
        this.showTableContent = true;
        this.subMeeting.push(entity);
        let filterId = {
          apartmentId : parseInt(this.cookieService.get('apartmentId'))
        }
        this.meetingService.getMeetingFiltersByApartmentId(filterId).subscribe((filterList:any)=>{
          let filter = filterList.filter((ele)=>  res[0].filterId == ele.filterId);
          if(filter.length > 0) {
            this.roleTypeArr.selectedItems = this.roleTypeArr.dropdownList.filter((role)=> role.value == filter[0].roleTypeId);
            let apartment = filter[0].apartmentBlockIds.split(',');
            apartment.forEach((ele1)=>{
              this.apartmentBlock.dropdownList.forEach((ele2)=>{
                if(parseInt(ele1) == ele2.apartmentBlockId) {
                  this.apartmentBlock.selectedItems.push(ele2)
                }
              })
            })
            let user={
              'ApartmentId': parseInt(this.cookieService.get('apartmentId')),
              'RoleTypeId': this.roleTypeArr.selectedItems[0].value
            }
            this.userService.getApartmentRolesByRoleTypeId(user).subscribe((userRole:any) => {
              this.roleCategory.dropdownList = userRole;
              let conditinArr= [{"name":"IsLiving","value":1},
                                {"name":"IsPrimaryContact","value":2}];
              if(parseInt(this.roleTypeArr.selectedItems[0].value)==2 || parseInt(this.roleTypeArr.selectedItems[0].valuee)==4){
                this.condition.dropdownList=conditinArr;
              }
              let roleId =  filter[0].roleIds.split(',');
              roleId.forEach((ele1)=>{
                this.roleCategory.dropdownList.forEach((ele2)=>{
                  if(parseInt(ele1) == ele2.value) {
                    this.roleCategory.selectedItems.push(ele2)
                  }
                })
              })
              let conditions = filter[0].conditions.split(',');
              conditions.forEach((ele1)=>{
                this.condition.dropdownList.forEach((ele2)=>{
                  if(parseInt(ele1) == ele2.value) {
                    this.condition.selectedItems.push(ele2)
                  }
                })
              })
              this.updateUsers();
            });
          }
        })
      })
    }
    this.roleTypedropdownSettings={
      singleSelection: true,
      primaryKey: 'value',
      labelKey: 'name',
      enableSearchFilter: true,
      enableFilterSelectAll: false,
      autoPosition: false,
      maxHeight:240
    }; 
    this.blockdropdownSettings={
      singleSelection: false,
      primaryKey: 'apartmentBlockId',
      labelKey: 'apartmentBlockNumber',
      enableSearchFilter: true,
      enableFilterSelectAll: false,
      autoPosition: false,
      maxHeight:240
    }; 
    this.roleDropdownSettings={
      singleSelection: false, 
      enableSearchFilter: true,
      labelKey: 'name',
      primaryKey: 'value',
      enableFilterSelectAll: false,
      autoPosition: false,
      maxHeight:240
    }; 
    this.dropdownSettings={
      singleSelection: false, 
      enableSearchFilter: true,
      labelKey: 'name',
      primaryKey: 'value',
      groupBy:"category",
      enableFilterSelectAll: false,
      autoPosition: false,
      maxHeight:240
    }; 
  }
}
